import React, { Suspense, lazy} from 'react'
import {Redirect, Switch} from "react-router-dom";
import Loading from "../../../components/shared-components/Loading";
import VillaRoute from "../../../permissions/VillaRoute";
const RoomsViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'content'}/>}>
            <Switch>
                <VillaRoute path={`${match.url}/create`} component={lazy(()=> import("./rooms-create/index"))}/>
                <VillaRoute path={`${match.url}/list`} component={lazy(()=> import("./rooms-list/index"))}/>
                <VillaRoute path={`${match.url}/p/:id`} component={lazy(()=> import("./rooms-detail/index"))}/>
                <VillaRoute path={`${match.url}/u/:id`} component={lazy(()=> import("./rooms-update/index"))}/>
                <VillaRoute path={`${match.url}/queue`} component={lazy(()=> import("./rooms-queue/index"))}/>
                <VillaRoute path={`${match.url}/active/:id`} component={lazy(()=> import("./rooms-active-detail/index"))}/>
                <VillaRoute path={`${match.url}/active`} exact component={lazy(()=> import("./rooms-active/index"))}/>
                <Redirect to={`${match.url}/create`} from={`${match.url}`}/>
            </Switch>
        </Suspense>

    )
}

export default RoomsViews
